import React from 'react';
import styles from './Training.module.scss';
import PageLink from '../PageLink/PageLink';
import WhatsApp from '../../image/WhatsApp.svg';
import TrainingList from '../TrainingList/TrainingList';
import { arrTheory, arrPractice } from '../../utils/data';
import circleGreen from '../../image/Training/Ellipse_green.svg'
import circlePink from '../../image/Training/Ellipse_pink.svg'
import PropTypes from "prop-types";

export default function Training ({isOpen}) {
    return (
        <section className={styles.main}>
            <PageLink
                text={''}
            />
            <h2 className={styles.title}>      </h2>
            <div className={styles.box}>
                <p className={styles.text}>     ,   <span className={styles.text_green}>  ,</span>      <span className={styles.text_green}> ,</span>     <span className={styles.text_green}>  ,</span>    !</p>
                <p className={styles.text}>      <span className={styles.text_pink}>  ,</span>          .</p>
            </div>
            <div className={styles.image_box}>
                <h3 className={styles.image_title}>   ,       </h3>
                <div className={styles.image_tel}>
                    <p className={styles.phone_text}>8 (913) 944-93-63</p>
                    <img className={styles.phone_image} src={WhatsApp} alt = ' '/>
                </div>
            </div>
            <h2 className={styles.title}> </h2>
            <div className={styles.box_list}>
                <TrainingList
                    title={' (5 )'}
                    list={arrTheory}
                    image={circleGreen}
                    color={'green'}
                />
                <TrainingList
                    title={' (8 )'}
                    list={arrPractice}
                    image={circlePink}
                    color={'pink'}
                />
            </div>
            <div className={styles.box_price}>
                <h3 className={styles.image_title}> </h3>
                <div className={styles.box_button}>
                    <p className={styles.phone_text}>25 000 .</p>
                    <button className={styles.button} type='button' onClick={isOpen}>  </button>
                    <p className={styles.price_text}>&#42;     3 </p>
                </div>
            </div>
        </section>
    )
}

Training.propTypes = {
    isOpen: PropTypes.func.isRequired,
};