import React from "react";
import styles from './Help.module.scss'

export default function Help() {

    return (
        <section className={styles.main}>
            <h2 className={styles.title}> Body Time  :</h2>
            <div className={styles.box}>
                <div className={styles.box_image_big}>
                    <p className={styles.text_big}>    </p>
                </div>
                <div className={styles.box_big}>
                    <div className={styles.box_image}>
                        <p className={styles.text_big}>   </p>
                    </div>
                    <div className={styles.box_small}>
                        <p className={`${styles.text_small} ${styles.text_gray}`}> ,     </p>
                        <p className={styles.text_small}>    </p>
                    </div>
                </div>
            </div>
        </section>
    )

}