import React from 'react';
import { Link } from 'react-router-dom';
import header from './header.module.scss';
import logo from '../../image/logo.svg';
import WhatsApp from '../../image/WhatsApp.svg';
import tel from '../../image/tel.svg';
import location from '../../image/location.svg';
import PropTypes from "prop-types";

export default function Header({isOpen}) {
    return (
      <header className={header.header}>
        <div className={header.box}>
          <div className={header.menu__icon} onClick={isOpen}>
              <span></span>
          </div>
          <Link  to="/">
            <img className={header.logo} src={logo} alt = ''/>
          </Link>
          <nav className={header.nav}>
            <Link className={header.link} to='/services'></Link>
            <Link className={header.link} to='/training'></Link>
            <Link className={header.link} to='/price'></Link>
            <Link className={header.link} to='/contacts'></Link>
          </nav>
          <div className={header.phone_box}>
            <img className={header.image} src={tel} alt = ' '/>
            <img className={header.image} src={WhatsApp} alt = ' '/>
            <p className={header.phone_text}>8 (913) 944-93-63</p>
          </div>
          <div className={header.location_box}>
            <img className={header.image} src={location} alt = ' '/>
            <p className={header.location_text}>. , .  ,16, . 205, 2 .</p>
          </div>
          <Link className={header.link_price} to='price'></Link>
        </div>
    
      </header>
    );
  };

  Header.propTypes = {
    isOpen: PropTypes.func.isRequired,
};